    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

::-webkit-scrollbar{
  width: 5px;
  height: 0.3rem;
}

::-webkit-scrollbar-track{
  background: #161B22;
  background: var(--nav-clr);
}

::-webkit-scrollbar-thumb {
  background: white;
  background: var(--white-clr);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}


