/* Latest Products */

.latestProduct{
    position: relative;
    margin-bottom: 2em;
}

.latestRow{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    width: 65vw;
    margin: 0 auto;
    overflow-x: scroll;
    position: relative; 
    scroll-behavior: smooth;
    padding-left: 1em;
}



.latestRow::-webkit-scrollbar{
    width: 5px;
    height: 0.3rem;
}

.latestRow::-webkit-scrollbar-track{
    background: var(--nav-clr);
}

.latestRow::-webkit-scrollbar-thumb {
    background: var(--white-clr);
  }

 .latestRow::-webkit-scrollbar-thumb:hover {
    background: var(--white-clr);
  }

.rightSlider{
    position: absolute;
    font-size: 2.5em;
    right: 11.75rem;
    top: 45%;
    cursor: pointer;
    z-index: 1;
    width: 2.5rem;
    background: transparent;
    transition: all 0.2s;
    border: none;
    
}
.leftSlider{
    position: absolute;
    text-align: center;
    font-size: 2.5em;
    left: 11.75rem;
    top: 45%;
    cursor: pointer;
    z-index: 1;
    width: 2.5rem;    
    background: transparent;    
    transition: all 0.2s;
    border: none;
}


.leftSlider:hover,.rightSlider:hover{
    background: var(--btn-clr);
    border: 2px solid var(--black-clr);
}
    

